    font-display: swap;
}

/* bold */
@font-face {
    font-family: 'Muller';
    src: url('../fonts/MullerBold/MullerBold.eot');
    src: url('../fonts/MullerBold/MullerBold.eot?#iefix') format('embedded-opentype'),
